import { useState } from 'react';
import { useSelector } from 'react-redux';
import { Formik, Form, useField } from 'formik';
import * as Yup from 'yup';
import { Button, FormControl, FormGroup, FormLabel, FormText, Modal } from 'react-bootstrap';
import { Rating } from 'react-simple-star-rating';
import Swal from 'sweetalert2';

import { fetchConToken } from '../../../helpers/fetch';


const MyTextInput = ({ label, type, ...props }) => {
    const [field, meta] = useField(props);
    return (
        <FormGroup className="mt-4">
            <FormLabel as="h5" htmlFor={props.id || props.name}>{label}</FormLabel>
            <FormControl as={type} {...field} {...props} />
            {meta.touched && meta.error ? (
                <FormText className='text-danger'>{meta.error}</FormText>
            ) : null}
        </FormGroup>
    );
};

export const SummaryModal = ({ id, setModalShow, oldTitulo, oldComentario, oldRating, ...props }) => {

    const { uid } = useSelector(state => state.auth);

    const [rating, setRating] = useState(20);

    const handleRating = (rate) => {
        setRating(rate);
    }

    const handleSubmit = async (values) => {

        const enviar = await fetchConToken(`productos/${id}`, {
            titulo: values.titulo,
            comentario: values.comentario,
            rating,
            usuario: uid,
            fecha: new Date(),
        }, 'POST');
        const bodyEnviar = await enviar.json();

        if (bodyEnviar.msg) { // Si hay errores
            Swal.fire('Error', bodyEnviar.msg, 'error');
        } else {
            setModalShow("");
            Swal.fire('Valoracin enviada', "Gracias por compartir su experiencia", 'success');
        }

    }

    return (
        <Formik
            initialValues={{
                titulo: oldTitulo || '',
                comentario: oldComentario || '',
            }}
            validationSchema={Yup.object({
                titulo: Yup.string()
                    .max(20, 'Must be 20 characters or less')
                    .required('Requerido'),
                comentario: Yup.string()
                    .max(1000, 'Must be 1000 characters or less')
                    .required('Requerido'),
            })}
            onSubmit={handleSubmit}
        >
            <Modal
                {...props}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">
                        Escribir una opinin
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <h5>Valoracin general</h5>
                    <Rating
                        onClick={handleRating}
                        ratingValue={oldRating || rating}
                        allowHover={false}
                    />
                    <Form>
                        <MyTextInput
                            label="Aade un ttulo"
                            name="titulo"
                            placeholder="Qu es lo ms importante?"
                        />
                        <MyTextInput
                            label="Aadir una resea escrita"
                            name="comentario"
                            type="textarea"
                            rows="3"
                            placeholder="Qu te ha gustado y qu no? Para qu usaste este producto?"
                        />
                        <Button className="mt-4" type="submit" variant="primary" size="lg">Enviar</Button>
                    </Form>
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={props.onHide}>Cerrar</Button>
                </Modal.Footer>
            </Modal>
        </Formik>
    );
}