import { useDispatch, useSelector } from 'react-redux';
import { Formik, Form, useField } from 'formik';
import * as Yup from 'yup';
import { Button, FormControl, FormGroup, FormLabel, FormText, Form as FormRB, Row, Col } from 'react-bootstrap';
import Swal from 'sweetalert2';

import { fetchConToken } from '../../../helpers/fetch';
import { shippingModalChange } from '../../../actions/ui';
import { shippingSetActive, shippingSetDefault, shippingStartAddBilling, shippingStartAddNew, shippingStartSort, shippingStartUpdate } from '../../../actions/shipping';


const MyTextInput = ({ label, type, ...props }) => {
    const [field, meta] = useField(props);
    return (
        <FormGroup className="mt-2">
            <FormLabel htmlFor={props.id || props.name}>{label}</FormLabel>
            <FormControl as={type} {...field} {...props} />
            {meta.touched && meta.error ? (
                <FormText className='text-danger'>{meta.error}</FormText>
            ) : null}
        </FormGroup>
    );
};

export const ShippingForm = () => {

    const dispatch = useDispatch();

    const { uid, nombre } = useSelector(state => state.auth);
    const { activo } = useSelector(state => state.shipping);

    const handleRegister = async ({ direccion, nombre, telefono }) => {

        let newEnvio;
        let body;
        let predeterminado;

        if (!activo) {
            newEnvio = { direccion, nombre, telefono };
            await dispatch(shippingStartAddNew(newEnvio)).then((result) => {
                predeterminado = result;
            });
        } else {
            const { _id } = activo;
            newEnvio = { _id, direccion, nombre, telefono };
            dispatch(shippingStartUpdate(newEnvio));
            predeterminado = _id;
        }

        if (document.querySelector("input[name=checkbox1]:checked")) { // Marcar direccin de envo como predeterminada
            dispatch(shippingSetDefault(predeterminado));
            newEnvio = { predeterminado }
            const resp = await fetchConToken(`usuarios/${uid}`, newEnvio, 'PUT');
            body = await resp.json();
            if (body.msg) { // Si hay errores
                console.log('Hola Mundo');
                Swal.fire('Error', body.msg, 'error');
            } else {
                const enviar = body.usuario.envio.find(element => element._id === predeterminado);
                dispatch(shippingStartUpdate(enviar));
                dispatch(shippingStartSort());
            }
        }

        const id = document.querySelector("input[name=checkbox2]:checked")?.id; // Direccin de facturacin igual a la direccin de envo
        if (id) {
            dispatch(shippingStartAddBilling(direccion));
        }

        dispatch(shippingModalChange(false));
        dispatch(shippingSetActive());

    }

    const handleAuto = (setFieldValue) => {
        setFieldValue("nombre", nombre);
        setFieldValue("telefono", "666666666");
        setFieldValue("direccion.calle", "Calle Marina");
        setFieldValue("direccion.numero", "Nmero 7, 8 A");
        setFieldValue("direccion.pais", "Espaa");
        setFieldValue("direccion.codigo", "21003");
        setFieldValue("direccion.poblacion", "Huelva");
        setFieldValue("direccion.provincia", "Huelva");
    }

    return (
        <Formik
            initialValues={{
                direccion: {
                    poblacion: activo?.direccion.poblacion || '',
                    pais: activo?.direccion.pais || '',
                    calle: activo?.direccion.calle || '',
                    numero: activo?.direccion.numero || '',
                    codigo: activo?.direccion.codigo || '',
                    provincia: activo?.direccion.provincia || '',
                },
                nombre: activo?.nombre || '',
                telefono: activo?.telefono || '',
            }}
            validationSchema={Yup.object({
                nombre: Yup.string()
                    .required('Requerido'),
                telefono: Yup.number()
                    .test('len', 'El telfono debe tener 9 nmeros', val => val && val.toString().length === 9)
                    .typeError('Debe especificar un nmero')
                    .required('Requerido'),
                direccion: Yup.object({
                    poblacion: Yup.string()
                        .required('Requerido'),
                    pais: Yup.string()
                        .required('Requerido'),
                    calle: Yup.string()
                        .required('Requerido'),
                    numero: Yup.string()
                        .required('Requerido'),
                    codigo: Yup.number()
                        .test('len', 'El cdigo debe tener 5 nmeros', val => val && val.toString().length === 5)
                        .typeError('Debe especificar un nmero')
                        .required('Requerido'),
                    provincia: Yup.string()
                        .required('Requerido'),
                })
            })}
            onSubmit={handleRegister}
        >
            {({ setFieldValue }) => (
                <Form>
                    <Button className='float-end' variant="warning" onClick={() => handleAuto(setFieldValue)}>Rellenar automticamente</Button>
                    <h4 style={{ "display": "inline" }}>Datos personales</h4>
                    <Row className='mt-2'>
                        <Col>
                            <MyTextInput
                                label="Nombre"
                                name="nombre"
                                placeholder="Nombre*"
                            />
                        </Col>
                        <Col>
                            <MyTextInput
                                label="Telfono"
                                name="telefono"
                                placeholder="Telfono*"
                            />
                        </Col>
                    </Row>
                    <h4 className='mt-4'>Direccin de envo</h4>
                    <Row className='mt-2'>
                        <Col>
                            <MyTextInput
                                label="Direccin"
                                name="direccion.calle"
                                placeholder="Direccin*"
                            />
                        </Col>
                        <Col>
                            <MyTextInput
                                label="Informacin adicional"
                                name="direccion.numero"
                                placeholder="Piso, escalera, puerta, etc.*"
                            />
                        </Col>
                    </Row>
                    <Row className='mt-2'>
                        <Col>
                            <MyTextInput
                                label="Pas"
                                name="direccion.pais"
                                placeholder="Pas*"
                            />
                        </Col>
                        <Col>
                            <MyTextInput
                                label="Cdigo postal"
                                name="direccion.codigo"
                                placeholder="Cdigo postal*"
                            />
                        </Col>
                    </Row>
                    <Row className='mt-2'>
                        <Col>
                            <MyTextInput
                                label="Poblacin"
                                name="direccion.poblacion"
                                placeholder="Poblacin*"
                            />
                        </Col>
                        <Col>
                            <MyTextInput
                                label="Provincia"
                                name="direccion.provincia"
                                placeholder="Provincia*"
                            />
                        </Col>
                    </Row>
                    <div key="checkbox-default" className="mt-4 mb-4">
                        <FormRB.Check
                            defaultChecked
                            type="checkbox"
                            id="checkbox1"
                            name="checkbox1"
                            label="Establecer esta direccin como predeterminada (para futuras compras)"
                        />
                        <FormRB.Check
                            defaultChecked
                            type="checkbox"
                            id="checkbox2"
                            name="checkbox2"
                            label="Usar mismos datos de envo para la facturacin"
                        />
                    </div>
                    <div className="d-grid mt-4">
                        <Button type="submit">
                            Guardar direccin
                        </Button>
                    </div>
                </Form>
            )}
        </Formik>
    );
}